.navbar {
    display: flex;
    justify-content: space-around;
    align-items: center;
    padding: 15px 40px;
    background-color: var(--secondary-color);  /* Using the secondary color (white) */
    box-shadow: 0px 3px 15px rgba(0, 0, 0, 0.1);  /* Box shadow */
    width: 80%;
    margin:20px auto;
}
.navbar a {
    margin: 0 15px;
    text-decoration: none;
    color: var(--secondary-text);
    font-size: 18px;
    
    transition: color 0.3s;
}

.navbar a:first-child{
font-weight: bolder;
color: var(--primary-color);
filter: brightness(60%);
}

.navbar a:hover {
    color: #333;  /* Slightly lighter black for hover effect */
}


.hindi-button {
    position: relative;
    /* left: 0; */
    top: -8px;
    left: 35px;
    background-color: var(--primary-color);
    color: var(--secondary-color);  /* Black text color for better contrast */
    border: none;
    border-radius: 5px;
    padding: 5px 15px;
    cursor: pointer;
    transition: background-color 0.3s;
}

.hindi-button:hover {
    background-color: #005500;  /* Darker green for hover effect */
}

