.header {
    background-color: var(--primary-color);
    padding: 20px 40px; 
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 1rem;
    border-radius: 20px;
}

.header-logo {
    flex-shrink: 0; 
    
    
    border-radius: 25px;
    
    width: 120px;
    height: auto;
}



.header-text {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    flex-grow: 1; 
    font-size: 2rem;
    
}

.header-text-hindi,
.header-text-english {
    color: var(--secondary-color);
    font-size: 20px; /* Adjust this based on your design preference */
    font-weight: bold;
    font-size: calc(2.1vw + 2.1vh);
    margin: 2px 0; /* Spacing between the Hindi and English text */
}
