.sliderDiv{
    margin: 10px;
    border: 10px;;
}
/* Carousel.css */
.slick-slide img {
    display: block;
    width: 100%;
    height: auto;
}

/* Style for active dot */
.slick-dots li.slick-active button:before {
    color: var(--primary-color);
    opacity: 1;
}

/* Style for other dots */
.slick-dots li button:before {
    color: black; /* Or any other color you prefer */
    opacity: 0.5;
}

/* Target the main container of the carousel */
.slick-slider {
    /* height: 400px;  Adjust this value to your preferred banner height */
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 10px; 
    border-radius: 10px;
    overflow: hidden; /* This ensures the images and other inner contents also get the border-radius effect */
}



/* Ensure the images also take the border-radius for a smoother look */
.slick-slide img {
    display: block;
    border-radius: 10px;
    max-height: 100%;  /* Ensures the image doesn't exceed the slide's height */
    min-width: 100%;   /* Ensures the image covers the slide's width */
    width: auto;       /* Auto width to maintain aspect ratio */
    height: auto;      /* Auto height to maintain aspect ratio */
    object-fit: cover; /* Adjusts the image's size and position to fill the slide without breaking aspect ratio */
    height: 400px;
}


/* Carousel.css */

/* Arrows */
.slick-prev,
.slick-next {
    z-index: 1; /* Ensures the arrows are above the slides */
    color: black; /* Sets arrow color */
    
}


/* Dots */
.slick-dots {
    bottom: 40px; /* Adjust this if the dots are too close or too far from the carousel */
}

/* Carousel.css */

.slick-arrow, .slick-dots {
    z-index: 2;
}

.slick-dots li button:before {
    font-size: 15px; /* Adjusts the dot size */
    color: grey; /* Non-active dots color */
}

.slick-next{
    right: 5px;
}
.slick-prev{
    left: 5px;
}

