.content{
    background-color: #fffdf1;
}
.content-section {
    padding: 20px;
    position: relative;  /* This allows positioning of child elements relative to this container */
    max-width: 1000px;
    margin: auto;
    background-color: #fffdf1;
}

.section-heading {
    font-weight: bolder;
    font-size: 2.5rem;
    color: var(--primary-color);
    margin-bottom: 10px;
}

.section-paragraph {
    font-size: 18px;
    margin-bottom: 20px;
    /* font-weight: bold; */
    color: var(--secondary-text);
}

.float-content{
    display: flex;
}

.demographics {
    width: 400px;  /* Should match the map width */
    height: 300px; /* Should match the map height */
    border-radius: 5px;
    
}
