.notice-bar {
    display: flex;
    align-items: center;
    
    color: var(--secondary-color);  /* Adjust as per your design */
    padding: 10px;
    margin: 10px;
    ;
}

.notice-heading {
    border-radius: 10px;
    background-color: #FE6A01;
    font-weight: bold;
    margin-right: 20px;
    padding: 15px 30px;
    font-size: 1.5rem;
}

.notice-content {
    
    flex: 1;
    width: 100%;
    overflow: hidden;
    background-color: var(--primary-color);
    border-radius: 10px;
    padding: 10px 10px;
    /* position: relative; */
}

.notice-text{
    border-radius: 10px;
    padding: 10px;
    white-space: nowrap;
    display: block; /* Important to give the content a width */
    width: 100%;
    transform: translateX(100%); /* Animation start out of the screen */
    /* Add the animation */
    animation: move 20s linear infinite /* infinite make reapeat the animation indefinitely */;
  }
  
  /* Create the animation */
  @keyframes move {
    to { transform: translateX(0%); }
  }

@keyframes scrollNotice {
    0% {
        transform: translateX(-50%);
    }
    100% {
        transform: translateX(-100%);
    }
}
